a {
  -webkit-user-modify: read-only;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

[hidden] {
  display: none !important;
}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}

html {
  font-size: 62.5%;
  position: relative;
  scroll-behavior: smooth;
  scroll-padding-top: 8rem;
}

body {
  font-family: "GPP text", sans-serif;
  font-size: 1.6rem;
  line-height: 1.2;
  word-break: break-word;
  word-wrap: break-word;
  color: #000;
  background: #fff;
  overflow-y: scroll;
  cursor: default;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, p {
  margin: 0;
}

.lazy-load {
  opacity: 0;
  transition: opacity 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.lazy-load--loaded {
  opacity: 1;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes pulse {
  to {
    transform: scale(1.1);
  }
}
@keyframes increase {
  from {
    transform: translate(-50%, 0) scaleX(0.1);
    opacity: 1;
  }
  to {
    transform: translate(125%, 0) scaleX(1);
    opacity: 1;
  }
}
@keyframes decrease {
  from {
    transform: translate(-125%, 0) scaleX(1);
    opacity: 1;
  }
  to {
    transform: translate(75%, 0) scaleX(0.1);
    opacity: 1;
  }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Khand", sans-serif;
}

section {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f2f2f2;
}
section .card {
  border-radius: 5%;
  background: #fff;
  position: relative;
  padding: 25px;
  box-shadow: -5px 20px 35px -9px rgb(66, 68, 90);
}
section .card .card-content {
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
section .card .card-content .playerinfo {
  background: #f1f3f4;
  background: radial-gradient(circle, rgb(241, 243, 244) 0%, rgb(255, 255, 255) 100%);
  color: #000000;
  padding: 10px 30px;
  font-size: 16px;
  line-height: 16px;
  box-sizing: content-box;
  text-align: center;
}
section .card .card-content .playerinfo h4 {
  font-size: 18px;
  line-height: 20px;
  margin: 0 0 5px 0;
}
section .card .card-content .playerinfo p {
  margin: 0;
}
section .card .card-content .playerinfo .playernumber {
  float: right;
  padding: 5px;
  font-size: 12px;
  line-height: 14px;
  text-transform: uppercase;
  text-align: center;
  position: absolute;
  top: 6px;
  right: 12px;
  color: #2a2a2a;
}
section .card .card-content .playerinfo .playernumber span {
  color: #e2007a;
  font-size: 70px;
  line-height: 60px;
  display: block;
}
section .card .image {
  height: 140px;
  width: 140px;
  border-radius: 10%;
}
section .card .image img {
  padding-bottom: 10px;
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 10%;
}

.navbar {
  transition: all 0.5s;
  /*&__hamburger {
      // position: absolute;
      right: 0;
      z-index: 2;
      display: block;
      width: 3.2rem; height: 3.2rem;
      overflow: hidden;
      text-indent: 100%;
      white-space: nowrap;
      cursor: pointer;

      background-size: 100%;

      @include mq(\$min-sm) {
          display: none;
      }
  }*/
}
@media (min-width: 1180px) {
  .navbar {
    width: 100%;
    margin: auto 0 auto auto;
  }
}
@media (min-width: 1180px) {
  .navbar__menu {
    display: block;
  }
}
@media (min-width: 1180px) {
  .navbar__contact {
    display: none;
  }
}
@media (min-width: 1180px) {
  .navbar__items {
    flex-direction: row;
    position: static;
    margin: auto;
    height: auto;
    background-color: transparent;
    justify-content: center;
    align-items: center;
    padding: 0;
  }
}
@media (min-width: 1180px) {
  .navbar__item {
    margin: 0 1rem;
  }
}
.navbar__link {
  color: #000;
}
@media (min-width: 1180px) {
  .navbar__link {
    color: #fff;
  }
}

.navbar.is--scrolled {
  padding-top: 0.8rem;
  padding-bottom: 0.8rem;
  background-color: #01A7C1;
}

#menu-state:checked + .navbar__menu {
  display: block;
}

#menu-state:checked ~ .navbar__hamburger {
  background-size: contain;
}

#logo {
  max-width: 100%;
  height: auto;
}

.header {
  display: flex;
